body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100vh;
  overflow-x: hidden;
  font-size: $fz-main;
  line-height: $lh-main;
  font-family: $ff-main;
  color: $primary-color;
}

main {
  flex-grow: 1;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

svg {
  display: block;
}

button:focus {
  outline: none;
}

